<template>
  <div id="app">
    <div class="canvas-bg" ref="bg">
      <div class="player" :canPlay="String(canPlay)" @click="handlePlayerClick">
        <i class="play fa fa-play"></i>
      </div>
    </div>
    <canvas class="canvas-music" ref="music"></canvas>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      // 播放器实例对象
      azusa: null,
      // 是否可以播放了，根据资源loaded进度
      canPlay: false,
      // 是否首次播放
      firstPlay: true,
      // 播放按钮锁定状态 防止重复点击
      playLock: false,
      // 延迟首次点击按钮后，延迟播放的时间配置, 单位毫秒ms
      delayTime: Object.freeze(600)
    }
  },
  mounted () {
    // dom对象
    const musicDom = this.$refs.music
    const container = this.$refs.bg
    // 实例化播放器对象
    this.azusa = new window.Azusa({
      view: musicDom,
      subdivisionSize: 1024,
      cutEnd: 256
    })
    // 加载背景图
    container.style.backgroundImage = `url(${require('@/assets/bg2.jpg')})`
    // 资源加载和配置项
    this.azusa.audio.load({
      src: require('@/assets/bg.mp3'),
      onPrgress: (xhr) => {
        const percent = (xhr.loaded / xhr.total).toFixed(4) * 100
        console.log(`--- 已加载 ${xhr.loaded}(${percent}%) 总大小 ${xhr.total}`)
        if (percent === 100) {
          this.canPlay = true
          console.log('音乐文件已加载完成, 可以点击播放...')
        }
      }
    })
    // 设置音量
    this.azusa.audio.setVolume(1)
    // 窗口发生大小改变 随之改变播放器大小
    window.addEventListener('resize', this.handleWindowResize)
  },
  destroyed () {
    // 组件离开后停止监听resize
    window.removeEventListener('resize', this.handleWindowResize)
  },
  methods: {
    // 窗口宽度发生改变回调
    handleWindowResize () {
      this.azusa.resize(
        document.documentElement.clientWidth,
        document.documentElement.clientHeight
      )
    },
    // 点击播放
    handlePlayerClick (event) {
      if (this.playLock) return console.log('请勿点击过快')
      if (!this.canPlay) return console.log('资源还没准备好, 请在加载完毕后播放')
      // 加操作锁
      this.playLock = true
      // 点击的对象
      const currentDom = event.target
      if (Array.from(currentDom.classList).includes('fa-play')) {
        // 首次播放需要fix bug 延迟调用播放函数
        if (this.firstPlay) {
          setTimeout(() => {
            // 开始播放
            this.azusa.audio.play()
            // 更新状态 释放锁
            this.firstPlay = false
            this.playLock = false
          }, this.delayTime)
        } else {
          // 开始播放
          this.azusa.audio.play()
          // 更新状态 释放锁
          this.playLock = false
        }
        // 更新UI 添加暂停css类
        currentDom.classList.add('fa-pause')
        currentDom.classList.remove('fa-play')
      } else {
        // 暂停播放
        this.azusa.audio.stop()
        // 更新状态 释放锁
        this.playLock = false
        // 更新UI 添加播放css类
        currentDom.classList.add('fa-play')
        currentDom.classList.remove('fa-pause')
      }
    }
  }
}
</script>

<style lang="scss" scope>
.canvas-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
  mix-blend-mode: screen;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
  background-origin: padding-box;
  background-attachment: scroll;
  box-shadow: 0 0 1000px #000 inset;
}

.canvas-bg .player {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.canvas-bg .player .play {
  font-size: 100px;
  color: #fff;
  z-index: 2;
  opacity: 0;
  cursor: pointer;
  transition: opacity linear 0.6s;
}

.canvas-bg .player .play:hover {
  opacity: 1;
}

.canvas-music {
  position: absolute;
  top: 0;
  left: 0;
}
</style>
